import  { useState } from "react";
import { BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, Legend } from "recharts";

const BMICalculator = () => {
  const [weight, setWeight] = useState("");
  const [height, setHeight] = useState("");
  const [gender, setGender] = useState("");
  const [bmi, setBMI] = useState(null);

  const calculateBMI = () => {
    const heightInMeters = height / 100;
    const bmiValue = (weight / (heightInMeters * heightInMeters)).toFixed(2);
    setBMI(bmiValue);
  };

  const handleFormSubmit = (e) => {
    e.preventDefault();
    calculateBMI();
  };

  const getBmiRange = () => {
    if (!bmi) return null;
    if (bmi < 18.5) return "Underweight";
    if (bmi >= 18.5 && bmi < 25) return "Normal Weight";
    if (bmi >= 25 && bmi < 30) return "Overweight";
    return "Obese";
  };

  const bmiData = [
    {
      name: "BMI",
      value: Number(bmi),
      fill: getBmiRange() === "Obese" ? "red" : "green",
    },
  ];

  return (
    <div className="bg-gradient-to-r from-gray-700 via-gray-900 to-black min-h-screen">
      <div className="container mx-auto px-4 py-8">
        <h1 className="text-3xl font-extrabold mb-4 text-yellow-200">
          BMI Calculator
        </h1>
        <form onSubmit={handleFormSubmit} className="mb-4">
          <div className="flex flex-col mb-4">
            <label htmlFor="weight" className="font-bold mb-2 text-yellow-200">
              Weight (in kgs)
            </label>
            <input
              type="number"
              id="weight"
              value={weight}
              onChange={(e) => setWeight(e.target.value)}
              className="border border-gray-300 rounded py-2 px-3"
              required
            />
          </div>
          <div className="flex flex-col mb-4">
            <label htmlFor="height" className="font-bold mb-2 text-yellow-200">
              Height (in cm)
            </label>
            <input
              type="number"
              id="height"
              value={height}
              onChange={(e) => setHeight(e.target.value)}
              className="border border-gray-300 rounded py-2 px-3"
              required
            />
          </div>
          <div className="flex flex-col mb-4">
            <label htmlFor="gender" className="font-bold mb-2 text-yellow-200">
              Gender
            </label>
            <select
              id="gender"
              value={gender}
              onChange={(e) => setGender(e.target.value)}
              className="border border-gray-300 rounded py-2 px-3"
              required
            >
              <option value="">Select gender</option>
              <option value="male">Male</option>
              <option value="female">Female</option>
            </select>
          </div>
          <button
            type="submit"
            className="bg-blue-500 hover:bg-green-600 text-white py-2 px-4 rounded"
          >
            Calculate BMI
          </button>
        </form>
        {bmi && (
  <div className="flex flex-col items-center">
    <h2 className="text-2xl font-bold mb-2 text-yellow-200">Your BMI: {bmi}</h2>
    <div style={{ width: "600px", height: "400px", marginBottom: "20px" }}>
      <BarChart width={600} height={400} data={bmiData}>
      <CartesianGrid strokeDasharray="3 3" />
                <XAxis dataKey="name" />
                <YAxis />
                <Tooltip />
                <Legend />
                <Bar dataKey="value" fill={bmiData[0].fill} />

      </BarChart>
    </div>
    <h3 className="text-xl font-semibold mb-2 text-yellow-200">
      BMI Range: {getBmiRange()}
    </h3>
  </div>
)}

      </div>
    </div>
  );
};

export default BMICalculator;
