import React from 'react';

const Advantage = ({ imageSrc, title, description, reverse }) => {
  return (
    <div className={`flex mt-2 flex-col lg:flex-row ${reverse ? 'lg:flex-row-reverse' : ''}`}>
      <div className="flex justify-center items-center lg:w-1/2">
        <img src={imageSrc} alt={title} className="w-72 h-auto" />
      </div>
      <div className="lg:w-1/2  py-10 px-8 flex flex-col justify-center bg-white bg-opacity-30 backdrop-filter backdrop-blur-md">
        <h3 className="text-3xl font-bold mb-4 text-yellow-200">{title}</h3>
        <p className='text-gray-300'>{description}</p>
      </div>
    </div>
  );
};

const AdvantagesPage = () => {
  return (
    <>
    
    <div className='bg-gradient-to-r from-gray-700 via-gray-900 to-black min-h-screen'>
    <h1 className='md:text-6xl text-white mb-2 text-center font-extrabold'>So why should you workout?</h1>
    <div className="container mx-auto px-4 py-12 mt-4">
      <Advantage
        imageSrc='all.webp'
        title="Social Engagement"
        description="Joining fitness classes, sports teams, or workout communities provides opportunities for social interaction, motivation, and accountability. Connecting with like-minded individuals who share similar fitness goals can make the workout experience more enjoyable and rewarding.."
      />
      <Advantage
    
        imageSrc='sleep.png'
        title="Better Sleep"
        description="Physical activity promotes better sleep quality, helping individuals fall asleep faster and enjoy a deeper, more restorative sleep. Regular exercise can combat insomnia and regulate sleep patterns, leading to improved overall sleep health."
      />
      <Advantage
        imageSrc='dumbell.png'
        title="Disease Prevention"
        description="Leading an active lifestyle significantly reduces the risk of chronic conditions like hypertension, stroke, certain types of cancer, and osteoporosis. Exercise strengthens the immune system, improves bone density, and supports overall disease prevention."
        />
      <Advantage
        imageSrc='weight.png'
        title="Weight Management"
        description="Regular physical activity, combined with a balanced diet, plays a crucial role in weight management. Workouts help burn calories, build lean muscle mass, and increase metabolism, supporting healthy weight loss or maintenance goals"      />
      <Advantage
        imageSrc='1.png'
        title="Increased Energy Levels"
        description="Exercise enhances blood circulation and oxygen flow, providing a natural energy boost. Regular workouts can combat feelings of fatigue, improve productivity, and enhance overall daily performance."      />
      <Advantage
        imageSrc='mental.png'
        title="Enhance Mental Well-being"
        description="Exercise is known to release endorphins, the feel-good hormones that elevate mood and reduce stress. It can help alleviate symptoms of anxiety and depression, improve cognitive function, and boost self-esteem, leading to a positive and balanced mindset."

      />
      <Advantage
        imageSrc='healthy.png'
        title="Boost Physical Health"
        description="Engaging in regular workouts helps improve cardiovascular health, strengthen muscles, and enhance flexibility and endurance. It reduces the risk of chronic diseases like heart disease, obesity, and diabetes, promoting overall physical well-being."
      />
    </div>
    </div>
    </>
  );
};

export default AdvantagesPage;
