import React, { useState } from 'react';
import { CSVLink } from 'react-csv';

const WorkoutPlanner = () => {
  const [workoutData, setWorkoutData] = useState([
    { day: '', exercises: [{ exercise: '', sets: '', reps: '' }] },
  ]);

  const handleInputChange = (e, dayIndex, exerciseIndex) => {
    const { name, value } = e.target;
    const newData = [...workoutData];
    newData[dayIndex].exercises[exerciseIndex][name] = value;
    setWorkoutData(newData);
  };

  const handleAddExercise = (dayIndex) => {
    const newData = [...workoutData];
    newData[dayIndex].exercises.push({ exercise: '', sets: '', reps: '' });
    setWorkoutData(newData);
  };

  const handleRemoveExercise = (dayIndex, exerciseIndex) => {
    const newData = [...workoutData];
    newData[dayIndex].exercises.splice(exerciseIndex, 1);
    setWorkoutData(newData);
  };

  const handleAddDay = () => {
    setWorkoutData([...workoutData, { day: '', exercises: [{ exercise: '', sets: '', reps: '' }] }]);
  };

  const handleRemoveDay = (dayIndex) => {
    const newData = [...workoutData];
    newData.splice(dayIndex, 1);
    setWorkoutData(newData);
  };

  const csvHeaders = [
    { label: 'Day', key: 'day' },
    { label: 'Exercise', key: 'exercise' },
    { label: 'Sets', key: 'sets' },
    { label: 'Reps', key: 'reps' },
  ];

  const csvData = workoutData.flatMap((day) =>
    day.exercises.map((exercise) => ({
      day: day.day,
      exercise: exercise.exercise,
      sets: exercise.sets,
      reps: exercise.reps,
    }))
  );

  return (
    <div className='bg-gradient-to-r from-gray-700 via-gray-900 to-black min-h-screen'>
    <div className="container mx-auto px-4 py-12 ">
      {workoutData.map((day, dayIndex) => (
        <div key={dayIndex} className="mb-8">
          <h2 className="text-2xl font-bold mb-4 text-yellow-200">Day {dayIndex + 1}</h2>
          <table className="w-full table-fixed">
            <thead>
              <tr>
                <th className="w-1/4 text-yellow-200">Exercise</th>
                <th className="w-1/4 text-yellow-200">Sets</th>
                <th className="w-1/4 text-yellow-200">Reps</th>
                <th></th>
              </tr>
            </thead>
            <tbody>
              {day.exercises.map((exercise, exerciseIndex) => (
                <tr key={exerciseIndex}>
                  <td>
                    <input
                      type="text"
                      name="exercise"
                      value={exercise.exercise}
                      onChange={(e) => handleInputChange(e, dayIndex, exerciseIndex)}
                      className="w-full p-2 border border-gray-300 rounded"
                    />
                  </td>
                  <td>
                    <input
                      type="text"
                      name="sets"
                      value={exercise.sets}
                      onChange={(e) => handleInputChange(e, dayIndex, exerciseIndex)}
                      className="w-full p-2 border border-gray-300 rounded"
                    />
                  </td>
                  <td>
                    <input
                      type="text"
                      name="reps"
                      value={exercise.reps}
                      onChange={(e) => handleInputChange(e, dayIndex, exerciseIndex)}
                      className="w-full p-2 border border-gray-300 rounded"
                    />
                  </td>
                  <td>
                    <button
                      className="bg-red-500 hover:bg-red-600 text-white py-2 px-4 rounded"
                      onClick={() => handleRemoveExercise(dayIndex, exerciseIndex)}
                    >
                      Remove
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
          <button
            className="bg-green-500 hover:bg-green-600 text-white py-2 px-4 mt-4 rounded"
            onClick={() => handleAddExercise(dayIndex)}
          >
            Add Exercise
          </button>
          {dayIndex > 0 && (
            <button
              className="bg-red-500 hover:bg-red-600 text-white py-2 px-4 mt-4 rounded ml-5"
              onClick={() => handleRemoveDay(dayIndex)}
            >
              Remove Day
            </button>
          )}
        </div>
      ))}
      <div className="flex justify-between mt-4">
      <button
        className="bg-green-500 hover:bg-green-600 text-white py-2 px-4 mt-4 rounded"
        onClick={handleAddDay}
      >
        Add Day
      </button>
      {workoutData.length > 0 && (
        <CSVLink
          data={csvData}
          headers={csvHeaders}
          filename="workout_plan.csv"
          className="bg-blue-500 hover:bg-blue-600 text-white py-2 px-4 mt-4 rounded"
          target="_blank"
          
        >
          Download CSV
        </CSVLink>
      )}
      </div>
    </div>
    </div>
  );
};

export default WorkoutPlanner;
